import { useFormik } from "formik";
import { useMemo } from "react";
import { useDispatch } from "react-redux";
import Input from "../../components/Input";
import { addMeal } from "../../redux/mealSlise";
import { calcCaloriesEnding } from "../../utils/lingvo";
import { prepareValues } from "../../utils/math";
import { MealsInputValues } from "../../utils/types";
import { onKeyPress } from "../../utils/utils";
import { mealsValidationSchema } from "../../utils/validation";
import {
  Flex,
  FormContainer,
  Grid,
  SaveButton,
  Text,
  Title,
} from "./Form.styles";

function Form() {
  const dispatch = useDispatch();

  const formik = useFormik({
    initialValues: {
      name: "",
      startWeight: 100,
      protein: "",
      fat: "",
      carb: "",
      weight: 100,
    } as MealsInputValues,
    validationSchema: mealsValidationSchema,
    onSubmit: (values, { resetForm }) => {
      dispatch(addMeal({ ...(values as any), calories }));
      resetForm();
    },
  });

  const { name, startWeight, protein, fat, carb, weight } = formik.values;

  const calories = useMemo(() => prepareValues(formik.values), [formik.values]);

  return (
    <FormContainer onSubmit={formik.handleSubmit}>
      <Title>Название продукта:</Title>
      <Input
        formik={formik}
        value={name}
        name="name"
        placeholder="Название"
        type={"text"}
        onFocus={() => formik.setFieldValue("name", "")}
      />
      <Flex>
        <Text>БЖУ на</Text>
        <Input
          formik={formik}
          value={startWeight}
          name="startWeight"
          placeholder="-Граммы"
          type={"number"}
          onKeyPress={(evt) => onKeyPress(evt, formik)}
          onFocus={() => formik.setFieldValue("startWeight", "")}
          startWeight={true}
        />
        <Text>грамм</Text>
      </Flex>

      <Grid>
        <Input
          formik={formik}
          value={protein}
          name="protein"
          placeholder="Белки"
          type={"number"}
          onKeyPress={(evt) => onKeyPress(evt, formik)}
          onFocus={() => formik.setFieldValue("protein", "")}
        />
        <Input
          formik={formik}
          value={fat}
          name="fat"
          placeholder="Жиры"
          type={"number"}
          onKeyPress={(evt) => onKeyPress(evt, formik)}
          onFocus={() => formik.setFieldValue("fat", "")}
        />
        <Input
          formik={formik}
          value={carb}
          name="carb"
          placeholder="Углеводы"
          type={"number"}
          onKeyPress={(evt) => onKeyPress(evt, formik)}
          onFocus={() => formik.setFieldValue("carb", "")}
        />
      </Grid>
      <Title>Вес продукта:</Title>
      <Input
        formik={formik}
        value={weight}
        name="weight"
        placeholder="Граммы"
        type={"number"}
        onKeyPress={(evt) => onKeyPress(evt, formik)}
        onFocus={() => formik.setFieldValue("weight", "")}
      />
      <SaveButton
        type="submit"
        aria-label="Добавить запись"
        disabled={!formik.isValid}
      >
        {` Добавить ${
          calories === 0 || Number.isNaN(calories)
            ? `немного килокалорий`
            : `${calories} ${calcCaloriesEnding(calories)}`
        }`}
      </SaveButton>
    </FormContainer>
  );
}

export default Form;
