import { useFormik } from "formik";
import { useState } from "react";
import { useDispatch } from "react-redux";
import Input from "../../components/Input";
import { editMeal } from "../../redux/mealSlise";
import { useEventListener } from "../../utils/eventListener";
import { prepareValues } from "../../utils/math";
import { MealPopupProps } from "../../utils/types";
import { onKeyPress } from "../../utils/utils";
import { mealsValidationSchema } from "../../utils/validation";
import {
  Close,
  Data,
  SavedData,
  EditButton,
  Flex,
  Form,
  Grid,
  MealPopupContainer,
  MealPopupOverlay,
  SaveButton,
  Text,
  Label,
} from "./MealPopup.style";

function MealPopup({ data, isOpen, onClose, onKeydown }: MealPopupProps) {
  const dispatch = useDispatch();

  const [editedForm, setEditedForm] = useState(false);

  useEventListener(isOpen, onKeydown, "keydown");

  const formik = useFormik({
    initialValues: {
      name: data?.name!,
      startWeight: 100,
      protein: (data?.protein! * 100) / data?.weight!,
      fat: (data?.fat! * 100) / data?.weight!,
      carb: (data?.carb! * 100) / data?.weight!!,
      weight: data?.weight!,
    },
    enableReinitialize: true,
    validationSchema: mealsValidationSchema,
    onSubmit: (values, { resetForm }) => {
      dispatch(editMeal({ ...values, id: data!.id, calories }));
      onClose();
      setEditedForm(false);
      resetForm();
    },
  });

  const { name, startWeight, protein, fat, carb, weight } = formik.values;

  const calories = prepareValues(formik.values);

  return (
    <MealPopupOverlay opened={isOpen} onClick={onClose}>
      <MealPopupContainer opened={isOpen} onClick={(e) => e.stopPropagation()}>
        <Close
          onClick={() => {
            onClose();
            setEditedForm(false);
          }}
        ></Close>
        {editedForm ? (
          <Form onSubmit={formik.handleSubmit}>
            <Input
              formik={formik}
              value={name}
              name="name"
              placeholder="Название"
              type={"text"}
              onFocus={() => formik.setFieldValue("name", "")}
            />
            <Flex>
              <Text>БЖУ на</Text>
              <Input
                formik={formik}
                value={startWeight}
                name="startWeight"
                placeholder="-Граммы"
                type={"number"}
                onKeyPress={(evt) => onKeyPress(evt, formik)}
                onFocus={() => formik.setFieldValue("startWeight", "")}
                startWeight={true}
              />
              <Text>грамм</Text>
            </Flex>
            <Grid>
              <Input
                formik={formik}
                value={protein}
                name="protein"
                placeholder="Белки"
                type={"number"}
                onKeyPress={(evt) => onKeyPress(evt, formik)}
                onFocus={() => formik.setFieldValue("protein", "")}
              />
              <Input
                formik={formik}
                value={fat}
                name="fat"
                placeholder="Жиры"
                type={"number"}
                onKeyPress={(evt) => onKeyPress(evt, formik)}
                onFocus={() => formik.setFieldValue("fat", "")}
              />
              <Input
                formik={formik}
                value={carb}
                name="carb"
                placeholder="Углеводы"
                type={"number"}
                onKeyPress={(evt) => onKeyPress(evt, formik)}
                onFocus={() => formik.setFieldValue("carb", "")}
              />
            </Grid>
            <Input
              formik={formik}
              value={weight}
              name="weight"
              placeholder="Граммы"
              type={"number"}
              onKeyPress={(evt) => onKeyPress(evt, formik)}
              onFocus={() => formik.setFieldValue("weight", "")}
            />
            <SaveButton
              type="submit"
              aria-label="Сохранить изменение"
              disabled={!formik.isValid}
            >
              Сохранить изменение
            </SaveButton>
          </Form>
        ) : (
          <SavedData>
            <Data>{data?.name!}</Data>
            <Flex>
              <Text>Рассчитанные данные:</Text>
            </Flex>
            <Data>{data?.calories!} ккал</Data>
            <Grid>
              <Data>
                <Label>белки</Label>
                {data?.protein!} г.
              </Data>
              <Data>
                <Label>жиры</Label>
                {data?.fat!} г.
              </Data>
              <Data>
                <Label>углеводы</Label>
                {data?.carb!} г.
              </Data>
            </Grid>
            <EditButton
              type="button"
              aria-label="Редактировать"
              onClick={() => setEditedForm(true)}
            >
              Редактировать
            </EditButton>
          </SavedData>
        )}
      </MealPopupContainer>
    </MealPopupOverlay>
  );
}

export default MealPopup;
